<template>
  <div class="container content pt-5">
    <div class="row">
      <div class="col-md-12 sobre">
        <div class="headline pb-1">
          <h2>Por que Balco Virtual?</h2>
        </div>
  
        <p>O Balco Virtual do TRF2  um site que permite o envio de peties intercorrentes em lote.</p>
  
        <div class="headline pb-1">
          <h2>Como fao para usar?</h2>
        </div>
  
        <p>Para fazer protocolar peties intercorrentes, siga o seguinte procedimento:
        </p>
        <ol>
          <li>Renomeie os arquivos PDF que contm as peties intercorrentes de modo que ele iniciem com o nmero do processo ao qual a petio se refere. Depois do nmero do processo, pode vir qualquer outra indicao do contedo do arquivo. Exemplos de nomes de arquivo para peties intercorrentes: 0527484-17.2007.4.02.5101.pdf, 01004835520144020000.pdf</li>
          <li>Caso deseje incluir anexos, aps o nmero do processo, insira a informao do nmero do anexo, na forma "-01", "-02", etc. Exemplos de nome de arquivo anexo  petio intercorrente: 0527484-17.2007.4.02.5101-01.pdf
          </li>
          <li>Selcione a opo de menu "Petio Intercorrente"</li>
          <li>Clique na rea marcada com "Arraste suas peties intercorrentes em formato PDF e solte elas nesta rea, ou clique aqui para selecion-las" e escolha os arquivos que deseja enviar.</li>
          <li>Selecione o tipo da petio e diga se h segredo de justia para cada um dos arquivos.</li>
          <li>Se desejar enviar uma mesma petio para mais de um processo, use um nome de arquivo que no contenha nenhum nmero de processo. Na interface, ao lado do arquivo aparecer um boto com smbolo "+". Clique nesse boto e fornea um lista dos nmeros de todos os processos onde quer protocolar esta petio e clique em "Prosseguir".
            <li>Clique no boto "Protocolar" e observe a coluna "Status" para saber se as peties foram protocoladas ou se ocorreu algum erro.
            </li>
        </ol>
        <p>
          Ateno, o Balco Virtual mantm o usurio autenticado por 18h aps o
          <i>login</i>. Fechar o navegador no causa automaticamente um
          <i>logout</i>. Para forar a desconexo do usurio,  necessrio clicar na opo "Logout" do menu principal.
        </p>
        <div class="headline pb-1 pt-3">
          <h2>Sobre o Balco Virtual</h2>
        </div>
  
        <p>O Balco Virtual foi desenvolvido em 2017 pela Coordenadoria de Inovao (CINOVA), parte da Secretaria de Tecnologia da Informao do Tribunal Regional Federal da 2 Regio, com a colaborao do Procurador Regional Substituto da Procuradoria Regional da Fazenda Nacional na 2 Regio, Dr. Joo Henrique Chauffaille Grognet.</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'sobre'
}
</script>

